/* Search Form from Search page */
.filmSearch{
    font-size: 1.2em;
  }
  
  form, #results, .alert {
    margin-left: 5rem;
    margin-right: 5rem;
  }
  
  form input{
    width: 50%;
  }
  
  .form-group{
    justify-content: center;
    text-align: center;
  }
  
  @media screen and (min-width: 768px){
    .form-group{
      display:flex
    }
    .form-control{
      width: 50%;
      margin-left: 1em;
    }
    .filmSearch{
      font-size: 2em;
    }
  }
  
  .btn-success{
    max-height: 38px !important;
  }