.bannerImage {
    margin: 1em 0 1em 0;
    position: relative;
    max-height: 55vh;
    min-height: 20em;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 42.29%, #FFFFFF 100%),url("../../images/shoppiesBanner.gif");
    background-size: cover;
    
    background-position: 50%
}

.BannerTitle {
    position: absolute;
    top: 42%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 3em;
}

.BannerDescription {
    position: absolute;
    top: 53%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-style: italic;
    font-size: 1.2em;
    /* font-size: 1.2em; */
    text-align: center;
}

@media screen and (min-width: 768px) {
    .bannerImage{
        min-height: 30em;
    }
}