rcase;font-size:.9rem;display:block}.promotion-price-text{padding:0;margin:0 auto;font-weight:900}.card-animate{counter-reset:section}.card-animate .card-body:before{transition:.5s ease;counter-increment:section;content:"" counter(section) "";display:block;font-size:15rem;font-weight:900;position:absolute;bottom:5rem;line-height:0;left:-.85rem;padding:0;margin:0;color:rgba(0,0,0,0.1);z-index:0}.card-animate .card:hover .card-body:before{transform:translate(10px, -10px)}.card-animate .card-text{margin-top:2rem;margin-bottom:2rem}.card-animate .card-title{font-weight:900;text-transform:uppercase}.customButton{display:inline-block;padding:2em 2em;border:1px solid #000;background:#eee}.card-body{padding:0 !important}.movieCard{margin-top:3rem;margin-top:3rem;min-width:300px !important}.resultsImg{width:100%}

.navbar{background:linear-gradient(to right, #E9F8FF, #90B2D8, #C1E3FF);box-shadow:0 10px 20px rgba(0,0,0,0.19),0 6px 6px rgba(0,0,0,0.23)}

small {
    color: grey;
}
.footer-container {
    background-color: #242B64;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-self: center;
    color: white;
}

.footer-links {
    width: 100%;
    display: flex;
    justify-content: center;
    color: white;
}

.footerLogo{
    max-width: 200px;
    margin: auto;
}

.footer-links h2 {
    color: white;
    font-weight: 100;
}

.footer-links h2:hover {
    color: red;
}

.footer-links a {
    text-decoration: none;
}

.footer-link-wrapper{
    text-align: center;  
    margin-left: 1em;  
}

.footer-socials {
    width: 100%;
    text-align: center;
}

.footer-socials-wrapper {
    align-items: center;
}

.social-icons {
    margin: 1em auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px
}

.social-icon-link {
    color: white;
    font-size: 34px
}

.social-icon-link:hover {
    color: red;
}

@media screen and (min-width: 768px) {
    .footer-link-wrapper{
        display: flex;
    }

    .footer-link-wrapper h2{
        margin-right: 3rem
    }
}

.filmSearch{font-size:1.2em}form,#results,.alert{margin-left:5rem;margin-right:5rem}form input{width:50%}.form-group{justify-content:center;text-align:center}@media screen and (min-width: 768px){.form-group{display:flex}.form-control{width:50%;margin-left:1em}.filmSearch{font-size:2em}}.btn-success{max-height:38px !important}

